import React from 'react'
import ReactDOM from 'react-dom'
import Auth from './Auth.jsx'
import { Verificate } from './utils/http'

ReactDOM.render(<Auth />, document.getElementById('weibo-container'))

Verificate().then(res => {
  window.opener.postMessage(
    JSON.stringify({ token: res.data.access_token }),
    window.opener.location
  )
}).catch(error => {
  window.opener.postMessage(
    JSON.stringify({ error }),
    window.opener.location
  )
})